<template>
  <div class="VmExpressionPicker">
    <UiPopover placement="bottom-start">
      <template #trigger>
        <UiItem
          class="ui-clickable"
          icon="mdi:plus-circle"
          secondary="Agregar ..."
        />
      </template>

      <template #contents="{close}">
        <div class="launcher-popover-contents">
          <div class="launcher-statements">
            <UiItem
              class="ui-clickable"
              @click="close(); launchIf()"
              icon="mdi:help-rhombus"
              text="IF"
            />
          </div>

          <div class="launcher-functions">
            <UiItem
              v-for="fn in functions"
              :key="fn.name"
              v-bind="fn"
              class="ui-clickable"
              @click="close(); launchFunction(fn)"
            />
          </div>
        </div>
      </template>
    </UiPopover>
  </div>
</template>

<script>
import definitions from './definitions/index.js';
import { UiItem, UiPopover } from '../../../ui';

export default {
  name: 'VmExpressionPicker',
  components: { UiItem, UiPopover },

  data() {
    return {};
  },

  computed: {
    functions() {
      let retval = [];
      for (let fxName in definitions.functions) {
        retval.push(
          Object.assign({ name: fxName }, definitions.functions[fxName])
        );
      }
      return retval;
    },

    operators() {
      let retval = [];
      for (let opName in definitions.operators) {
        retval.push(
          Object.assign({ name: opName }, definitions.operators[opName])
        );
      }
      return retval;
    },
  },

  methods: {
    launchFunction(definition) {
      let expression = {
        call: definition.name,
        args: null,
      };
      this.$emit('input', { expression, definition });
    },

    launchIf() {
      let expression = {
        if: { and: [] },
        then: { do: null },
        else: { do: null },
      };

      this.$emit('input', {
        expression,
        definition: {
          icon: 'mdi:help-rhombus',
          text: 'IF',
        },
      });
    },

    // launchOperator(op) {
    //   this.$emit('input', {
    //     op: op.name,
    //     field: null,
    //     args: null,
    //   });
    // },
  },
};
</script>

<style lang="scss">
.VmExpressionPicker {
  .launcher-trigger {
    padding: var(--ui-padding);
  }

  .launcher-popover-contents {
    width: 480px;
    max-height: 500px;
    overflow: auto;
  }
}
</style>